.card {
  border-radius: 8px;
  background-color: var(--bg-button);
  padding: 14px 18px 14px 24px;
  width: 335px;
  box-sizing: border-box;
}
.card.none {
  border-left: 4px solid var(--priority-default);
}
.card.low {
  border-left: 4px solid #8fa1d0;
}

.card.medium {
  border-left: 4px solid #e09cb5;
}

.card.high {
  border-left: 4px solid #bedbb0;
}

.title {
  color: var(--primary-text-color);
  font-size: 14px;
  font-weight: 600;
  letter-spacing: -0.28px;
  line-height: normal;

  margin-bottom: 8px;
}

.text {
  color: var(--primary-text-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
  letter-spacing: -0.24px;
  margin-bottom: 19px;
  opacity: 0.7;

  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  white-space: normal;
  position: relative;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 14px;
  border-top: 1px solid var(--card-line);
}

.infoBox {
  display: flex;
  gap: 14px;
}

.priority {
  display: flex;
  gap: 4px;
}

.circle {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.circle.none {
  background-color: var(--priority-default);
}

.circle.low {
  background-color: #8fa1d0;
}

.circle.medium {
  background-color: #e09cb5;
}

.circle.high {
  background-color: #bedbb0;
}

.infoTitle {
  color: var(--primary-text-color);
  font-size: 8px;
  font-weight: 400;
  letter-spacing: -0.16px;
  margin-bottom: 5px;
  opacity: 0.5;
}

.info {
  color: var(--primary-text-color);
  font-size: 10px;
  font-weight: 400;
  letter-spacing: -0.2px;
}
.info::first-letter {
  text-transform: uppercase;
}
.iconList {
  display: flex;
  gap: 8px;
}

.button {
  width: 16px;
  height: 16px;
  border: none;
  padding: 0;
  background-color: transparent;
  color: var(--primary-text-color);
  opacity: 0.5;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  fill: var(--bg-button);

  &:hover {
    opacity: 1;
  }
}
