.main {
  background-size: cover;
  background-repeat: no-repeat;
  height: calc(100vh - 60px);
  background-color: var(--avatar-bg-color);
}

@media screen and (min-width: 768px) {
  .main {
    height: calc(100vh - 68px);
  }
}

@media screen and (min-width: 1440px) {
  .main {
   margin-left: 260px;
  }
}
